
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapState, mapActions } from 'vuex'
export default {
components: {
XInput,
XButton,
Group,
Cell,
Datetime,
Alert,
XAddress
},
data () {
return {
name:"",
phoneNumber:"",
birthday_myStr:"",
email:"",
chartData:{},
chartSettings:{},
showContent_1:true,
addressData: ChinaAddressData,
addressValue: []
}
},
computed: {
...mapState({
player: state => state.user.playerInfo,
userinfo: state => state.user.userinfo
})
},
mounted() {
if(this.userinfo.isSetOk === 0) {
this.showContent_1 = false
return
}
this.name = this.userinfo.name
this.phoneNumber = this.userinfo.phoneNumber
let dateStr = dateFormat(new Date(this.userinfo.birthday), 'YYYY-MM-DD')
this.birthday_myStr = dateStr.toString()
this.email =this.userinfo.email||""
this.addressValue = this.userinfo.addressValue || []
this.$refs.mybirthday._data.currentValue = dateStr
this.chartData = {
columns: ['日期', '销售额-1季度', '销售额-2季度', '占比', '其他'],
rows: [
{ '日期': '1月1日', '销售额-1季度': 1523, '销售额-2季度': 1523, '占比': 0.12, '其他': 100 },
{ '日期': '1月2日', '销售额-1季度': 1223, '销售额-2季度': 1523, '占比': 0.345, '其他': 100 },
{ '日期': '1月3日', '销售额-1季度': 2123, '销售额-2季度': 1523, '占比': 0.7, '其他': 100 },
{ '日期': '1月4日', '销售额-1季度': 4123, '销售额-2季度': 1523, '占比': 0.31, '其他': 100 },
{ '日期': '1月5日', '销售额-1季度': 3123, '销售额-2季度': 1523, '占比': 0.12, '其他': 100 },
{ '日期': '1月6日', '销售额-1季度': 7123, '销售额-2季度': 1523, '占比': 0.65, '其他': 100 }
]
}
this.chartSettings = {
dimension: ['日期'],
metrics: ['销售额-1季度', '销售额-2季度', '占比'],
axisSite: {
right: ['占比']
},
yAxisType: ['KMB', 'percent'],
yAxisName: ['销售额', '占比'],
area: true,
stack: {
'销售额': ['销售额-1季度', '销售额-2季度']
}
}
},
methods: {
...mapActions([
'updateUserInfo'
]),
showContent (type) {
this["showContent_" + type] = !this["showContent_" + type]
},
async submitInfo (val) {
let valid1 = this.$refs.myname.valid
let valid2 = this.$refs.myphone.valid
let valid3 = this.$refs.mybirthday.valid
if (!valid1) {
this.$vux.alert.show({
content: "请填写正确的名字"
})
return
}
if (!valid2) {
this.$vux.alert.show({
content: "请填写正确的手机号"
})
return
}
if (!valid3) {
this.$vux.alert.show({
content: "请填写正确的出生年月"
})
return
}
if(this.addressValue.length === 0) {
this.$vux.alert.show({
content: "请填写所在地址"
})
return
}
let birthdayNumber = new Date(this.birthday_myStr).getTime()
if(!birthdayNumber) {
this.$vux.alert.show({
content: "请填写正确的出生年月"
})
return
}
await this.updateUserInfo({
name:this.name,
phoneNumber:this.phoneNumber,
birthday:birthdayNumber,
addressValue:this.addressValue,
sex:this.player.sex,
email:this.email
})
}
}
}



